import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Icon } from '@iconify/react';

import axios from '../../utils/axios';
import { toast } from 'react-toastify';
import { HOST_API, API_TICKET, WEBSOCKET_CONSTATNT } from '../../config';
import useLocales from '../../hooks/useLocales';
import useWebsocket from '../../hooks/useWebsocket';

import { fNumber } from '../../utils/formatNumber';
import { fDescriptionTypeDate } from '../../utils/formatTime';

import { dispatch, useSelector } from '../../redux/store';
import { setAvailableCart } from '../../redux/slices/cart';

import Page from '../../components/Page';
import CountChooser from '../../components/form/CountChooser';
import PageBanner from '../../components/wrappers/PageBanner';
import EventSearchComponent from '../../components/form/EventSearchComponent';
import GradientBorderWrapper, { Divider } from '../../components/wrappers/GradientBorderWrapper';
import { FloatingInput } from '../../components/form/FloatingInput';

export default function Carts() {
    const { t } = useLocales();
    const navigate = useNavigate();
    const { received } = useWebsocket();

    const { cart } = useSelector((store) => store.cart);
    const [cartList, setCartList] = useState([]);
    const [isCupon, setIsCupon] = useState(false);
    const [processing, setProcessing] = useState(false);

    const onDelete = (id) => {
        setProcessing(true);
        const data = cartList.filter((e) => e._id !== id);
        dispatch(setAvailableCart(data));
        axios
            .post(API_TICKET.cart, { ticketId: id })
            .then(() => {
                getCart();
            })
            .finally(() => {
                setProcessing(false);
            });
    };

    const onNavigate = () => {
        navigate('/cart/payment');
    };

    const setAccount = (account, index) => {
        const datas = [...cart];
        datas[index] = { ...datas[index], account };
        dispatch(setAvailableCart(datas));
    };

    const getCart = async () => {
        setProcessing(true);
        await axios
            .get(API_TICKET.cart)
            .then(({ data, status }) => {
                if (status === 200) {
                    setCartList(data.data);
                    const tickets = data.data?.reduce((s, item) => [...s, item.ticket], []);
                    dispatch(setAvailableCart(tickets));
                }
            })
            .finally(() => {
                setProcessing(false);
            });
    }

    useEffect(() => {
        getCart();
        const timer = setInterval(getCart, 1 * 60 * 1000);
        return () => {
            clearInterval(timer);
        };
    }, []);

    useEffect(() => {
        if (received && received?.command) {
            const { command, id } = received;
            if (command === WEBSOCKET_CONSTATNT.REMOVE_CART) {
                const data = cartList.filter((e) => e.ticketId !== id);
                dispatch(setAvailableCart(data));
                setCartList(data);
                toast.warning(t("description.cart"));
            }
        }
    }, [received]);

    const onChangeCounter = () => { };

    const total = cart?.reduce((s, item) => s + item.ticket?.originPrice, 0);
    const disabled = !!cart?.find((e) => e.requireAccount && !e.account);

    return (
        <Page title="Carrinho">
            <PageBanner>
                <div className="container p-4 mb-8">
                    {cartList.length ? (
                        <div className="w-full justify-center flex flex-col mb-8">
                            <h4 className="text-[64px]">{t('title.menu.cart')}</h4>
                            <p className="text-stone-400 mt-2">
                                {t('description.cart')}
                            </p>
                            <div className="flex flex gap-8 mt-16">
                                <div className="flex flex-col gap-2 w-4/5">
                                    {cartList.map((item, key) => (
                                        <GradientBorderWrapper key={key}>
                                            <div className="p-6 flex gap-5 items-center">
                                                {/* <div className="avatar">
                                                <div className="rounded-full h-12 w-12">
                                                    <LazyLoadImage src={`${HOST_API}${item.ticket.avatar}`} alt="" />
                                                </div>
                                            </div> */}
                                                <div className="flex flex-col gap-1 w-full">
                                                    <div className="flex items-center justify-between">
                                                        <h5 className="text-[28px] capitalize">
                                                            {item.ticket.seat.length} X {item.event.name}
                                                        </h5>
                                                        {item.ticket.uploadedFile ? (
                                                            <Icon
                                                                icon="material-symbols:verified-user-rounded"
                                                                className="text-success w-8 h-8"
                                                            />
                                                        ) : (
                                                            <Icon
                                                                icon="ic:baseline-not-interested"
                                                                className="text-error w-8 h-8"
                                                            />
                                                        )}
                                                    </div>
                                                    <p className="capitalize text-stone-200">
                                                        {
                                                            item.event.types.find((e) => e.typeId === item.ticket.eventTypeId)
                                                                .name
                                                        }
                                                    </p>
                                                    <p className="text-stone-400 capitalize text-sm">
                                                        {fDescriptionTypeDate(item.ticket.dateTime)}
                                                    </p>
                                                    {/* <h6 className="text-stone-400 capitalize">
                                                        {item.ticket.seat?.join(' , ')} <br />
                                                    </h6> */}
                                                    <Divider cls="my-8" />
                                                    <div className="flex gap-2 justify-between mb-4">
                                                        <div className="flex gap-2">
                                                            <button
                                                                className="btn btn-circle btn-sm bg-red-500 hover:bg-red-400"
                                                                onClick={() => onDelete(item.ticket._id)}
                                                            >
                                                                <Icon
                                                                    icon="tabler:trash"
                                                                    className="text-red-800"
                                                                    width={15}
                                                                />
                                                            </button>
                                                            <div>
                                                                <p className="text-[19px] capitalize">
                                                                    R$ {fNumber(item.ticket.originPrice)}
                                                                </p>
                                                                <p className="text-stone-400 capitalize mt-1">Ingresso</p>
                                                            </div>
                                                        </div>

                                                        <div className="flex flex-col gap-1">
                                                            <CountChooser
                                                                current={item?.ticket.count}
                                                                onChanged={onChangeCounter}
                                                                size="sm"
                                                                // disabled={
                                                                //     item?.ticket.status === 'sold' ||
                                                                //     user?._id === item?.ticket.uploader
                                                                // }
                                                                disabled={true}
                                                            />
                                                        </div>
                                                    </div>
                                                    {item.ticket.requireAccount && (
                                                        <div className="flex flex-col gap-1">
                                                            <FloatingInput
                                                                required
                                                                type="text"
                                                                className="input w-full"
                                                                value={item.ticket?.account}
                                                                label={item.ticket?.requireAccountDetail}
                                                                onChange={(e) => setAccount(e.target.value, key)}
                                                            />
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        </GradientBorderWrapper>
                                    ))}

                                    <div className="flex items-center mt-5 justify-between">
                                        <div>
                                            <div className="flex items-center gap-2">
                                                <p className="text-[19px]">
                                                    {t('title.total')} R$ {fNumber(total)}
                                                </p>
                                                <div className="text-primary">
                                                    <Icon icon="mdi:information-variant-circle" width={20} />
                                                </div>
                                            </div>
                                            {!isCupon && (
                                                <p
                                                    className="text-primary underline pointer cursor-pointer mt-2"
                                                    onClick={() => setIsCupon(true)}
                                                >
                                                    {t('description.coupon')}
                                                </p>
                                            )}
                                        </div>
                                        <button
                                            disabled={disabled}
                                            className="btn btn-primary px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto"
                                            onClick={onNavigate}
                                        >
                                            {t('action.continue_shopping')}
                                        </button>
                                    </div>

                                    {isCupon && (
                                        <div className="flex items-center gap-4 mt-8">
                                            <GradientBorderWrapper className="rounded-2xl" >
                                                <input className="input w-full rounded-2xl py-4 px-6 text-base h-auto" />
                                            </GradientBorderWrapper>
                                            <button className="btn btn-primary btn-outline px-7 capitalize py-5 rounded-2xl text-[19px] h-auto">
                                                {t('action.apply_coupon')}
                                            </button>
                                            <button
                                                className="btn btn-circle btn-ghost btn-sm"
                                                onClick={() => setIsCupon(false)}
                                            >
                                                <Icon
                                                    icon="mdi:close-circle-outline"
                                                    className="text-red-600"
                                                    width={24}
                                                />
                                            </button>
                                        </div>
                                    )}
                                </div>
                                <div className="flex flex-col gap-8">
                                    <GradientBorderWrapper>
                                        <div className="w-full flex gap-4 p-6 items-center">
                                            <div className="avatar">
                                                <div className="rounded-full h-[50px] w-[50px]">
                                                    <LazyLoadImage src={`${HOST_API}${cartList[0].uploader?.avatar}`} alt="" />
                                                </div>
                                            </div>
                                            <div className="flex flex-col gap-1 min-w-[300px]">
                                                <h5 className="text-[19px] capitalize">{cartList[0].uploader?.fullName}</h5>

                                                <div className="text-stone-400 flex gap-1 text-sm items-center">
                                                    <Icon
                                                        icon={
                                                            cartList[0].uploader?.phoneVerification
                                                                ? `material-symbols:verified-user-rounded`
                                                                : `ic:baseline-not-interested`
                                                        }
                                                        className={`w-4 h-4 ${cartList[0].uploader?.phoneVerification ? 'text-success' : 'text-error'}`}
                                                    />
                                                    Telefone verificado
                                                </div>

                                                <p className='text-sm mt-3 text-stone-400'>
                                                    Ingresso LOTE 4
                                                </p>
                                            </div>
                                        </div>
                                    </GradientBorderWrapper>
                                    <GradientBorderWrapper className="event-active-bg">
                                        <div className="w-full flex p-6 gap-4">
                                            <div className="text-primary">
                                                <Icon icon="mdi:information-variant-circle" className="w-6 h-6" />
                                            </div>
                                            <div>
                                                <p className="text-[19px]"> Conta do ingresso necessária</p>
                                                <p className="text-stone-400 text-sm">
                                                    Para poder acessar isso, você precisa ter uma conta no site da xyz
                                                </p>
                                            </div>
                                        </div>
                                    </GradientBorderWrapper>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <>
                            <div className="w-full justify-center items-center flex flex-col mb-8">
                                <h4 className="text-5xl">{t('title.empty_cart')}</h4>
                                <p className="text-sm/[17px] max-w-[450px] text-center text-stone-400 mt-4">
                                    {t('description.empty_cart')}
                                </p>
                            </div>
                            <div className="container w-2/3 mt-16 mb-4">
                                <EventSearchComponent />
                            </div>
                        </>
                    )}
                </div>
            </PageBanner>
        </Page>
    );
}
